* {
  margin: 0;
  padding: 0;
  color: white;
}

body {
  background: url(i/sportBg.jpg) no-repeat;
  background-size: cover;
  background-repeat: no-repeat;
  font-family: Arial, Helvetica, sans-serif;
}

#root {
  height: 100vh;
  width: 100%;
  background: linear-gradient(0deg, rgba(84, 84, 84, 0) 0%, rgb(0, 0, 0) 100%);
}

#root::before {
  background-color: rgba(255, 0, 0, 0.87);
  mix-blend-mode: multiply;
  content: "";
  position: absolute;
  z-index: -1;
  height: 100vh;
  width: 100%;
}

#main {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0 auto;
}

#content {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 90vh;
}

#first {
  display: flex;
  width: 100%;
  padding: 2rem;
}

#second {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding: 2rem;
}

.item {
  display: flex;
}

#first .item {
  background-color: white;
  width: 100%;
  border-radius: 1rem;
}

#second .item {
  width: 100%;
  background-color: white;
  border-radius: 1rem;
}

.brand {
  margin: 0 auto;
}

header {
  padding: 2rem 1rem 1rem;
}

address {
  margin-top: 3rem;
}

